<template>
  <div>
  <div class="title">热销推荐</div>
    <ul>
      <router-link
      tag="li"
      class="item border-bottom"
      v-for="item of list"
      :to="'/detail/' + item.id"
      :key="item.id">
            <img class="item-img" :src="item.imgUrl"/>
        <div class="item-info">
            <p class="item-title">{{item.title}}</p>
            <p class="item-desc">{{item.desc}}</p>
            <button class="item-button">{{item.button}}</button>
        </div>
      </router-link>
    </ul>
    </div>
</template>

<script>
export default {
  name: 'HomeRecommend',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .title
    line-height: .8rem
    background: #eee
    text-indent: .2rem
    margin-top: .2rem
  .item
    display: flex
    overflow: hidden
    height: 1.9rem
    .item-img
      height: 1.7rem
      width: 1.7rem
      padding: .1rem
  .item-info
    min-width: 0
    flex: 1
    padding: .1rem
    .item-title
      line-height: .54rem
      font-size: .32rem
      ellipsis ()
    .item-desc
      line-height: .4rem
      color: #ccc
      ellipsis ()
    .item-button
      line-height: .44rem
      margin-top: .16rem
      background: #ff9300
      padding: 0 .1rem
      border-radius: .06rem
      color: #fff
</style>



